.center_container
{
    margin-top: 5rem;
}
.ttitle
{
  color: #000;
  text-align: center;
  position: relative;
  top: 2rem;
}
.aboutlines
{
    color: #000;
    position: relative;
    font-weight: bold;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    margin-left: 1rem;
    top: 4rem;
}
.course_container
{
  width: 100%;
  display: grid;
  position: absolute;
  top: 120%;
  align-items: center;
  justify-content: center;
  background: #fff;
}
.course_container h1
{
  color: #000;
  text-align: center;
  margin-top: 2rem;
}
.courselist
{
    display: grid;
    gap: 3rem;
    list-style-type: circle;
    margin-top: 1rem;
    grid-template-columns: repeat(4,1fr);
}
.enquirybtn
{
  position: relative;
  padding: 0.7rem 2.4rem;
  font-size: 1rem;
  margin-top: 0.9rem;
  border: 1px outset #fff;
  margin-left: 0.8rem;
  text-align: center;
  color: #fff;
  background:var(--color-primary);
  box-shadow: 0 3px 2px 0 rgba(31, 38, 135, 0.685);
  backdrop-filter: blur( 4.5px );
  -webkit-backdrop-filter: blur( 4.5px );
  border-radius: 20px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );
}

.main-review-wrapper h1
{
  color: #000;
  text-align: center;
  margin-top: 1rem;
  font-size: 2.4rem;
}
.reviewlist
{
  width: 50%;
  margin-top: 12rem;
  margin-left: 20rem;
}
.swiper {
  width: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
}

.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 300px;
  height: 300px;
}
.course_Card .list
{
  color: #000;
  font-size: 15px;
  position: relative;
  font-weight: bold;
  top:-10%;
  text-align: center;
}
.atitle
{
  margin-top: 2rem;
  font-size: 3rem;
  color: #000;
  text-align: center;
}
.bimgs
{
  display: grid;
  grid-template-columns: repeat(2,1fr);
  margin-bottom: 3rem;
}
.bosch
{
  width: 100%;
  margin-left: 2rem;
}
.boschbanner
{
  width: 60%;
  margin-top: 3rem;
  margin-left: 3rem;
}
