.main_header
{
    width: 100vw;
    height: calc(100vh - 15rem);
    display: grid;
    place-items: center;
    margin-top: 3rem;
}
.main_header-container
{
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    justify-content: space-between;
    gap: 6rem;
}
.main_header-left
{
    margin-top: 4rem;
}
.main_header-left h1
{
    font-family: 'SecularOne-Regular';
    color: #000;
    font-size: 5rem;
    font-family: 'Acme', sans-serif;
    text-transform: uppercase;
}

.main_header-left h3
{
    color: var(--color-secondary);
    font-weight: 400;
    font-size: 1.5rem;
    margin-top: 1rem;
    text-transform: uppercase;
    font-family: 'SecularOne-Regular';
}
.main_header-left p
{
    margin: 1rem 0 2rem;
    font-size: 3rem;
    font-family: 'Tangerine', sans-serif;
    font-weight: 'bold';
    color: #000;
}
.main_header-right
{
    display: grid;
    place-items: center;
    position: relative;
}
.main_header-image
{
    position: relative;
    margin-top: 2rem;
    animation: animate 2s linear infinite;
    width: 70%;
}
.htitle
{
    color: #000;
    margin-top: 2rem;
    font-size: 2rem;
    text-align: center;
    margin-left: 2rem;
}
@keyframes animate
{
  0%
  {
    filter: hue-rotate(0deg);
  }
  100%
  {
    filter: hue-rotate(360deg);
  }
}

/*************SERVICES*****************/

.services
{
    margin-top: 4rem;
}
.services_wrapper
{
    width: 100%;
    display: grid;
    margin-top: 1rem;
    gap: 1rem;
    align-items: center;
    justify-content: center;
    grid-template-columns: repeat(4,1fr);
}
.services_services h4
{
    margin-top: 2rem;
}
.services_services a
{
    margin-top: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
.services_services:hover a
{
    background: var(--color-secondary);
}
/*********************LOCATIONS*********************/
.location
{
    height: 45vh;
    width: 100vw;
    margin-top: 3rem;
    background-color: #fff;
    place-items: center;
    border-top: 1px solid black;
}
.location h2
{
    font-size: 2.3rem;
    position: absolute;
    left: 32%;
    color: #000;
    margin-top: 3rem;
}
.areas
{
    width: 100%;
    display: inline-block;
    list-style-type: none;
}
.location_areas
{
    display: grid;
    grid-template-columns: repeat(5,1fr);
    position: relative;
    color: #000;
    top: 7rem;
    left: 3rem;
}
.location_areas li
{
    margin-bottom: 1rem;
    font-size: 12px;
    margin-top: 0.4rem;
    color: #000;
    font-family: 'Hammersmith One', sans-serif;
    word-spacing: 1px;
}
.location_areas li h3
{
    color: #000;
}

/*******************FOOTER*******************/

footer
{
    background: #fff;
    margin-top: 0.1rem;
    padding-top: 3rem;
    font-size: 0.9rem;
    color: var(--color-gray-100);
}
.footer_container
{

    display: grid;
    grid-template-columns: 15rem 1fr 1fr 1fr;
    gap: 6rem;
}
.group
{
    color: #000;
    font-size: 2rem;
    margin-left: 1.5rem;
}
.flogo
{
    width: 9vw;
    margin-left: 4rem;
    filter: drop-shadow(0.9rem 0.5rem 1rem black);
}
.gname
{
    text-align: center;
}
.footer_container article
{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.7rem;
    color: #000;
}
.footer_container article h3
{
    color: #000;
}
.imail
{
    color: #000;
    font-weight: bold;
    font-size: 1rem;
    margin-top: 2rem;
    margin-left: -4rem;
    position: absolute;
}
.sfont
{
    color: #000;
    font-weight: bold;
}
.footer_container article .value
{
    margin-top: 0.8rem;
    font-size: 19px;
    position: relative;
    color: #000;
}
.footer_container article h4
{
    margin-bottom: 0.6rem;
    font-size: 1rem;
    color: #000;
}
.mapouter
{
    position: relative;
    margin-top: -22rem;
    margin-left: 30rem;
}
iframe
{
    width: 572px;
    height: 300px;
}
.footer_social
{
    margin-top: 1.5rem;
    display: flex;
    gap: 1rem;
    align-items: center;
}
.footer_social a
{
    background: var(--color-gray-100);
    padding: 0.7rem;
    border: 1px solid transparent;
    border-radius: 0.6rem;
    transition: var(--transition);
}
.footer_social a svg
{
    color: var(--color-gray-600);
}
.footer_social a:hover
{
    background: var(--color-gray-100);
    background-color: transparent;
}
.location_container .address
{
    font-size: 15px;
    position: absolute;
    color: #000;
    font-weight: bold;
    margin-top: 2.5rem;
    margin-right: 3rem;
}
.footer_copyright
{
    color: var(--color-gray-100);
    text-align: center;
    padding: 1rem 0;
    border-top: 3px dashed #fff;
    margin-top: 2rem;
    font-size: 1.2rem;
    text-shadow: 3px #fff;
    font-family: 'Secular One', sans-serif;
    background: #000;
}

