.service_container
{
    margin-top: 5rem;
    background: #fff;
}
.banner:hover
{
    filter:grayscale(10);
    transition: ease-in;
}
.servtitle
{
    font-weight: bold;
    text-align: center;
}
.servtitle h1
{
    color: aqua;
    animation: animate 2s linear infinite;
    margin-top: 1rem;
    letter-spacing: 1px;
    text-shadow: 0.2rem 0.1rem blue;
    text-transform: uppercase;
    font-family: 'Acme', sans-serif;
}
.servicelist_container
{
    width: 100%;
    display: grid;
    margin-top: 1rem;
    align-items: center;
    justify-content: center;
    background: #fff;
}
@keyframes animate
{
  0%
  {
    filter: hue-rotate(0deg);
  }
  100%
  {
    filter: hue-rotate(360deg);
  }
}
.servicelist
{
    display: grid;
    gap: 3rem;
    list-style-type: circle;
    margin-top: 4rem;
    grid-template-columns: repeat(4,1fr);
}
.services_card
{
    background: #fff;
    box-shadow: 0 8px 10px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 4.5px );
    border: 2px solid var(--color-gray-100);
    text-align: center;
    margin-top: 1rem;
    margin-bottom: 2rem;
    width: 20vw;
    margin-left: 0.4rem;
    height: 35vh;
    align-content: center;
    justify-content: center;
    border-radius: 10px;
    transition: var(--transition);
}
.services_card h4
{
    margin-bottom: 3rem;
    font-weight: 300;
    margin-top: 2rem;
    color: #000;
    font-family: 'Roboto Slab', serif;
}
.services_card span
{
    width: 8rem;
    height: 5rem;
    color: var(--color-gray-100);
    font-size: 1.5rem;
    padding: 0.8rem;
    place-items: center;
    margin-bottom: 1.5rem;
}
.services_card small
{
    margin-top: 7rem;
    display: block;
    color: #000;
    font-size: 9px;
    font-weight: 300;
    position: relative;
    font-family: 'Anton', sans-serif;
}
.service_icon
{
    width: 15%;
    height: 30px;
    position: absolute;
    margin-left: 1rem;
    margin-top: 2.6rem;
}
.servicebtn
{
    position: absolute;
    padding: 0.7rem;
    font-size: 13px;
    margin-top: 4rem;
    border: 1px outset #fff;
    margin-left: -3.2rem;
    text-align: center;
    color: #fff;
    background:var(--color-primary);
    box-shadow: 0 4px 8px 0 rgba(31, 38, 135, 0.685);
    backdrop-filter: blur( 4.5px );
    -webkit-backdrop-filter: blur( 4.5px );
    border-radius: 50px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
}
.servicebtn:hover
{
    border: 2px double white;
}
.wtitle
{
    color: #000;
    margin-top: 2rem;
    font-size: 2rem;
    text-align: center;
    margin-left: 2rem;
}
.wimgs
{
    display: grid;
    margin-top: 2rem;
    grid-template-columns: repeat(4,1fr);
    gap: 3rem;
    margin-bottom: 2rem;
}
.bank
{
    width: 80%;
    margin-left: 4rem;
    margin-top: 3.5rem;
}
.mahal
{
    width: 60%;
    margin-top: 2rem;
    margin-left: 5rem;
}
.finance
{
    width: 100%;
    margin-top: -1rem;
    margin-left: -1rem;
}
.cvd
{
    width: 60%;
    margin-top: 2rem;
    margin-bottom: 1rem;
    margin-left: 1rem;
}
@media screen and (min-width:1200px) and (max-width:1444px)
{
    .servicelist
    {   
        display: grid;
        gap: 1rem;
        margin-left: 2rem;
        list-style-type: circle;
        margin-top: 2rem;
        grid-template-columns: repeat(3,1fr);
    }
    .services_card
    {
        background:#fff;
        box-shadow: 0 3px 8px 0 rgba(31, 38, 135, 0.685);
        border: 1px solid gray;
