        margin-bottom: 1.5rem;
        margin-inline: auto;
        border-radius: 1rem;
        transition: var(--transition);
    }
    .servicebtn
    {
        position: absolute;
        padding: 0.7rem 2rem;
        font-size: 1rem;
        margin-top: 6rem;
        border: 1px outset #fff;
        margin-left: -6rem;
